body {
  --nav-link-height: 22.5px;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

.header {
  justify-content: space-between;
  width: 100%;
  display: flex;
}

.nav {
  margin-right: 50px;
}

.nav__list {
  justify-content: flex-end;
  align-items: flex-start;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.logo {
  justify-self: flex-start;
  width: 40px;
  height: 40px;
  margin: 10px 50px;
}

.nav__list__element {
  list-style: none;
  display: flex;
}

.nav__list__element:not(:last-child) {
  padding-right: 20px;
}

.nav__link {
  -webkit-text-decoration: inherit;
  text-decoration: inherit;
  padding: var(--nav-link-height) 0;
  color: #000;
  text-transform: uppercase;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background: #00acdc;
  border-radius: 8px;
  flex-shrink: 0;
  width: 37px;
  height: 4px;
  position: absolute;
  bottom: -1px;
  left: 0;
}

